<template>
    <div class="todoList">
            <el-radio-group v-model="type" size="medium" @change="tabChange">
                <el-radio-button label="0">未完成</el-radio-button>
                <el-radio-button label="1">已完成</el-radio-button>
                <el-radio-button label="2">全部</el-radio-button>
            </el-radio-group>
            <el-button type="success" round @click="newAdd=true;addMenuVisiable=true" style="margin-left:30px;" size="small">添加</el-button>
            <div class="item-list">
                <div class="item" v-for="item in itemList" :key="item.id">
                    <span style="flex:1">{{item.content}}</span>
                    <span style="margin:0 10px;">{{moment(item.updateTime).format('MMMM Do YYYY, h:mm:ss a')}}</span>
                    <el-button type="primary" icon="el-icon-edit" circle @click="edit(item.id,item.content)"></el-button>
                    <el-button type="danger" icon="el-icon-delete" circle @click="deleteItem(item.id)"></el-button>
                    <el-button type="success" icon="el-icon-check" circle v-if="item.type=='0'" @click="changeType(item.id,item.type)"></el-button>
                    <el-button type="info" icon="el-icon-close" circle v-else @click="changeType(item.id,item.type)"></el-button>
                </div>
            </div>
            <el-dialog
                    :title="newAdd?'添加待办':'编辑待办'"
                    :visible.sync="addMenuVisiable"
                    width="30%"
                    @close="dialogClose"
                    center>
                <el-input
                        v-model="newMenu"
                        type="textarea"
                        placeholder="请填写代办"
                        maxlength="100"
                        rows="4"
                        @keypress.enter="addConfirm"
                        ></el-input>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogClose">取 消</el-button>
                    <el-button type="primary" @click="addConfirm">确 定</el-button>
                </div>
            </el-dialog>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                type:'0',
                newAdd:true,
                addMenuVisiable:false,
                newMenu:'',
                itemList:[],
                currentItemId:''
            }
        },
        methods:{
            tabChange(){
                console.log(this.type)
                this.getItem()
            },
            changeType(id,type){
                this.$post('todoList/changeType',{
                    id,type
                })
                    .then(res=>{
                        console.log(res)
                        if(res.code==200){
                            this.$message.success('已完成');
                            this.getItem();
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            },
            addItem(){
               console.log('增加一条新数据');
               this.$post('todoList/addItem',{
                   content:this.newMenu,
                   type:'0'
               })
                   .then(res=>{
                       console.log(res)
                       if(res.code===200){
                           this.addMenuVisiable=false;
                           this.newMenu='';
                           this.type='0';
                           this.getItem()

                       }
                   })
                   .catch(err=>{
                       console.log(err)
                   })
            },
            edit(id,content){
                this.newAdd=false;
                this.addMenuVisiable=true;
                this.currentItemId=id;
                this.newMenu=content;
            },
            editItem(){
                console.log('看下id')
                console.log(this.currentItemId)
                this.$post('todoList/editItem',{
                    content:this.newMenu,
                    id:this.currentItemId
                })
                    .then(res=>{
                        console.log(res)
                        if(res.code===200){
                            this.addMenuVisiable=false;
                            this.$message.success('编辑成功')
                            this.newMenu='';
                            this.currentItemId=''
                            this.getItem()
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            },
            deleteItem(id){
                this.$post('todoList/deleteItem',{id})
                    .then(res=>{
                        if(res.code==200){
                            this.$message.success('删除成功');
                            this.getItem()
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                })
            },
            getItem(){
                this.$post('todoList/getItem',{
                    type:this.type
                })
                    .then(res=>{
                        if(res.code==200){
                            console.log(res)
                            this.itemList=res.data;

                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })

            },
            dialogClose(){
                this.addMenuVisiable=false;
                this.newMenu='';
            },
            addConfirm(){
                if(this.newAdd){
                    this.addItem();
                } else{
                    this.editItem();
                }
            }
        },
        mounted(){
            this.getItem()
        }
    }

</script>
<style lang="less" scoped>
    .todoList{
        margin-top: 20px;
        text-align:left;
        .item-list{
            .item{
                display:flex;
                align-items:center;
                padding:10px;
                margin:5px;
                border:solid 1px #efe1e1;
                box-shadow: 1px 1px 5px -1px rgba(0,0,0,0.46);
                .icon-button{
                    margin:0 5px;
                    cursor:pointer;
                }
            }

        }
    }

</style>
